/* ==========================================================================
   IMPORTS
   ========================================================================== */

@import url('https://fonts.googleapis.com/css2?family=Press+Start+2P&display=swap');
@import './variables.css';
@import './components.css';
@import './animations.css';

/* ==========================================================================
   RESET AND BASE STYLES
   ========================================================================== */

* {
  box-sizing: border-box;
  image-rendering: pixelated;
  -webkit-font-smoothing: none;
  -moz-osx-font-smoothing: grayscale;
}

*::before,
*::after {
  box-sizing: border-box;
  image-rendering: pixelated;
}

html {
  font-size: 16px;
  scroll-behavior: smooth;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--pixel-font);
  font-size: var(--pixel-font-size-base);
  line-height: 1.4;
  background: var(--pixel-gradient-bg);
  color: var(--pixel-gray-300);
  min-height: 100vh;
  overflow-x: hidden;
  image-rendering: pixelated;
}

/* ==========================================================================
   SCROLLBAR STYLING
   ========================================================================== */

::-webkit-scrollbar {
  width: 12px;
}

::-webkit-scrollbar-track {
  background: var(--pixel-gray-900);
  border: 1px solid var(--pixel-gray-700);
}

::-webkit-scrollbar-thumb {
  background: var(--pixel-green);
  border: 1px solid var(--pixel-green-dark);
}

::-webkit-scrollbar-thumb:hover {
  background: var(--pixel-green-dark);
}

/* Firefox */
html {
  scrollbar-width: thin;
  scrollbar-color: var(--pixel-green) var(--pixel-gray-900);
}

/* ==========================================================================
   TYPOGRAPHY
   ========================================================================== */

h1, h2, h3, h4, h5, h6 {
  font-family: var(--pixel-font);
  font-weight: bold;
  margin: 0 0 var(--pixel-spacing-lg) 0;
  line-height: 1.2;
  image-rendering: pixelated;
}

h1 {
  font-size: var(--pixel-font-size-4xl);
  color: var(--pixel-green);
}

h2 {
  font-size: var(--pixel-font-size-3xl);
  color: var(--pixel-green);
}

h3 {
  font-size: var(--pixel-font-size-2xl);
  color: var(--pixel-green);
}

h4 {
  font-size: var(--pixel-font-size-xl);
  color: var(--pixel-green);
}

h5 {
  font-size: var(--pixel-font-size-lg);
  color: var(--pixel-green);
}

h6 {
  font-size: var(--pixel-font-size-base);
  color: var(--pixel-green);
}

p {
  margin: 0 0 var(--pixel-spacing-lg) 0;
  line-height: 1.6;
}

a {
  color: var(--pixel-green);
  text-decoration: none;
  font-family: var(--pixel-font);
  transition: color 0.2s ease;
}

a:hover {
  color: var(--pixel-green-light);
  text-shadow: var(--pixel-glow);
}

a:focus {
  outline: 2px solid var(--pixel-green);
  outline-offset: 2px;
}

strong, b {
  font-weight: bold;
  color: var(--pixel-green);
}

em, i {
  font-style: italic;
  color: var(--pixel-gray-200);
}

code {
  font-family: var(--pixel-font);
  background-color: var(--pixel-gray-800);
  color: var(--pixel-green);
  padding: var(--pixel-spacing-xs) var(--pixel-spacing-sm);
  border: 1px solid var(--pixel-gray-600);
  border-radius: 2px;
  font-size: var(--pixel-font-size-sm);
}

pre {
  background-color: var(--pixel-gray-900);
  color: var(--pixel-green);
  padding: var(--pixel-spacing-lg);
  border: var(--pixel-border);
  border-radius: 4px;
  overflow-x: auto;
  font-family: var(--pixel-font);
  font-size: var(--pixel-font-size-sm);
  line-height: 1.4;
}

pre code {
  background: none;
  border: none;
  padding: 0;
}

/* ==========================================================================
   LISTS
   ========================================================================== */

ul, ol {
  margin: 0 0 var(--pixel-spacing-lg) 0;
  padding-left: var(--pixel-spacing-2xl);
}

li {
  margin-bottom: var(--pixel-spacing-sm);
  font-family: var(--pixel-font);
}

ul li::marker {
  content: "â–¸ ";
  color: var(--pixel-green);
  font-weight: bold;
}

ol li::marker {
  color: var(--pixel-green);
  font-weight: bold;
}

/* ==========================================================================
   FORMS
   ========================================================================== */

input, textarea, select, button {
  font-family: var(--pixel-font);
  image-rendering: pixelated;
}

input[type="text"],
input[type="email"],
input[type="password"],
input[type="number"],
input[type="tel"],
input[type="url"],
textarea,
select {
  width: 100%;
  padding: var(--pixel-spacing-md);
  background-color: var(--pixel-gray-900);
  color: var(--pixel-green);
  border: var(--pixel-border);
  font-size: var(--pixel-font-size-base);
  transition: background-color 0.2s, border-color 0.2s;
}

input[type="text"]:focus,
input[type="email"]:focus,
input[type="password"]:focus,
input[type="number"]:focus,
input[type="tel"]:focus,
input[type="url"]:focus,
textarea:focus,
select:focus {
  outline: none;
  background-color: var(--pixel-gray-700);
  border-color: var(--pixel-green-light);
}

input::placeholder,
textarea::placeholder {
  color: var(--pixel-gray-500);
  font-family: var(--pixel-font);
}

label {
  display: block;
  margin-bottom: var(--pixel-spacing-sm);
  color: var(--pixel-green);
  font-family: var(--pixel-font);
  font-weight: bold;
  font-size: var(--pixel-font-size-sm);
}

/* ==========================================================================
   TABLES
   ========================================================================== */

table {
  width: 100%;
  border-collapse: collapse;
  margin-bottom: var(--pixel-spacing-2xl);
  font-family: var(--pixel-font);
  font-size: var(--pixel-font-size-sm);
}

th, td {
  padding: var(--pixel-spacing-md);
  text-align: left;
  border: 1px solid var(--pixel-gray-600);
}

th {
  background-color: var(--pixel-gray-800);
  color: var(--pixel-green);
  font-weight: bold;
}

td {
  background-color: var(--pixel-gray-900);
  color: var(--pixel-gray-300);
}

tr:hover td {
  background-color: var(--pixel-gray-700);
}

/* ==========================================================================
   UTILITY CLASSES
   ========================================================================== */

.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  white-space: nowrap;
  border: 0;
}

.text-center {
  text-align: center;
}

.text-left {
  text-align: left;
}

.text-right {
  text-align: right;
}

.font-mono {
  font-family: var(--pixel-font);
}

.font-bold {
  font-weight: bold;
}

.uppercase {
  text-transform: uppercase;
}

.lowercase {
  text-transform: lowercase;
}

.capitalize {
  text-transform: capitalize;
}

.leading-tight {
  line-height: 1.25;
}

.leading-relaxed {
  line-height: 1.625;
}

.leading-loose {
  line-height: 2;
}

/* Color utilities */
.text-green {
  color: var(--pixel-green);
}

.text-red {
  color: var(--pixel-red);
}

.text-blue {
  color: var(--pixel-blue);
}

.text-yellow {
  color: var(--pixel-yellow);
}

.text-purple {
  color: var(--pixel-purple);
}

.text-gray-300 {
  color: var(--pixel-gray-300);
}

.text-gray-400 {
  color: var(--pixel-gray-400);
}

.text-gray-500 {
  color: var(--pixel-gray-500);
}

/* Background utilities */
.bg-transparent {
  background-color: transparent;
}

.bg-gray-800 {
  background-color: var(--pixel-gray-800);
}

.bg-gray-900 {
  background-color: var(--pixel-gray-900);
}

/* Spacing utilities */
.m-0 { margin: 0; }
.mt-0 { margin-top: 0; }
.mr-0 { margin-right: 0; }
.mb-0 { margin-bottom: 0; }
.ml-0 { margin-left: 0; }

.p-0 { padding: 0; }
.pt-0 { padding-top: 0; }
.pr-0 { padding-right: 0; }
.pb-0 { padding-bottom: 0; }
.pl-0 { padding-left: 0; }

/* Display utilities */
.hidden {
  display: none;
}

.block {
  display: block;
}

.inline-block {
  display: inline-block;
}

.flex {
  display: flex;
}

.inline-flex {
  display: inline-flex;
}

.grid {
  display: grid;
}

/* Flex utilities */
.items-center {
  align-items: center;
}

.items-start {
  align-items: flex-start;
}

.items-end {
  align-items: flex-end;
}

.justify-center {
  justify-content: center;
}

.justify-between {
  justify-content: space-between;
}

.justify-around {
  justify-content: space-around;
}

.flex-col {
  flex-direction: column;
}

.flex-wrap {
  flex-wrap: wrap;
}

.flex-1 {
  flex: 1 1 0%;
}

/* Position utilities */
.relative {
  position: relative;
}

.absolute {
  position: absolute;
}

.fixed {
  position: fixed;
}

.sticky {
  position: sticky;
}

/* Z-index utilities */
.z-0 { z-index: var(--z-background); }
.z-10 { z-index: var(--z-content); }
.z-20 { z-index: var(--z-fixed); }
.z-30 { z-index: var(--z-modal); }
.z-50 { z-index: var(--z-notification); }

/* Overflow utilities */
.overflow-hidden {
  overflow: hidden;
}

.overflow-x-hidden {
  overflow-x: hidden;
}

.overflow-y-hidden {
  overflow-y: hidden;
}

.overflow-auto {
  overflow: auto;
}

/* Width and height utilities */
.w-full {
  width: 100%;
}

.h-full {
  height: 100%;
}

.min-h-screen {
  min-height: 100vh;
}

/* Border radius utilities */
.rounded {
  border-radius: 4px;
}

.rounded-lg {
  border-radius: 8px;
}

.rounded-full {
  border-radius: 50%;
}

/* Shadow utilities */
.shadow {
  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.1), 0 1px 2px 0 rgba(0, 0, 0, 0.06);
}

.shadow-lg {
  box-shadow: 0 10px 15px -3px rgba(0, 0, 0, 0.1), 0 4px 6px -2px rgba(0, 0, 0, 0.05);
}

.shadow-2xl {
  box-shadow: 0 25px 50px -12px rgba(0, 0, 0, 0.25);
}

/* ==========================================================================
   RESPONSIVE DESIGN
   ========================================================================== */

/* Mobile first approach - base styles are for mobile */

/* Tablet */
@media (min-width: 768px) {
  html {
    font-size: 18px;
  }
  
  .md\:grid-cols-2 {
    grid-template-columns: repeat(2, minmax(0, 1fr));
  }
  
  .md\:grid-cols-3 {
    grid-template-columns: repeat(3, minmax(0, 1fr));
  }
  
  .md\:grid-cols-4 {
    grid-template-columns: repeat(4, minmax(0, 1fr));
  }
  
  .md\:flex-row {
    flex-direction: row;
  }
  
  .md\:text-left {
    text-align: left;
  }
}

/* Desktop */
@media (min-width: 1024px) {
  .lg\:grid-cols-2 {
    grid-template-columns: repeat(2, minmax(0, 1fr));
  }
  
  .lg\:grid-cols-3 {
    grid-template-columns: repeat(3, minmax(0, 1fr));
  }
  
  .lg\:col-span-2 {
    grid-column: span 2 / span 2;
  }
}

/* Large desktop */
@media (min-width: 1280px) {
  .max-w-6xl {
    max-width: 72rem;
  }
}

/* ==========================================================================
   ACCESSIBILITY
   ========================================================================== */

/* Focus styles */
button:focus,
a:focus,
input:focus,
textarea:focus,
select:focus {
  outline: 2px solid var(--pixel-green);
  outline-offset: 2px;
}

/* High contrast mode support */
@media (prefers-contrast: high) {
  :root {
    --pixel-green: #00ff00;
    --pixel-gray-900: #000000;
    --pixel-gray-300: #ffffff;
  }
}

/* Reduced motion support */
@media (prefers-reduced-motion: reduce) {
  * {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
  }
}

/* Print styles */
@media print {
  .no-print {
    display: none !important;
  }
  
  body {
    background: white !important;
    color: black !important;
  }
  
  .pixel-border {
    border: 1px solid black !important;
  }
}